    beforeUpload: function beforeUpload(file) {
      this.fileuploadDate.project_id = this.project_id;
      this.uploadDate.project_id = this.project_id;
      var filetypes = [".doc", ".docx", ".ppt", ".pptx", ".pdf", ".zip", ".rar"];
      var name = file.name;
      var fileend = name.substring(name.lastIndexOf(".")).toLowerCase();
      var isnext = false;
      if (filetypes && filetypes.length > 0) {
        for (var i = 0; i < filetypes.length; i++) {
          if (filetypes[i] == fileend) {
            isnext = true;
            break;
          }
        }
      }
      this.loading = false;
      if (!isnext) {
        this.$tool.error("不支持的文件格式");
        return false;
      }
      if (parseInt(file.size) > parseInt(20971521)) {
        this.$tool.error("暂不支持超过20M文件上传哦");
        return false;
      };
      this.uploadLoading = true;
      this.submitButton = true;
    },
